import React from "react"
import { Box, Slide, useScrollTrigger } from "@rp/react/web/mui/core"

export const HideOnScroll = ({
  children,
  offset = 0,
  zIndex = 10,
  disabled,
  threshold,
  sticky = true,
}: {
  children: React.ReactNode
  offset?: number
  zIndex?: number
  disabled?: boolean
  threshold?: number
  sticky?: boolean
}) => {
  const trigger = useScrollTrigger({ threshold })

  if (!sticky) {
    return (
      <Box sx={{ position: "relative", top: 0 + offset, zIndex: zIndex, backgroundColor: "red" }}>
        {children}
      </Box>
    )
  }

  return disabled ? (
    <Box sx={{ position: "sticky", top: 0 + offset, zIndex: zIndex }}>{children}</Box>
  ) : (
    <Slide appear={false} direction="down" in={!trigger}>
      <Box sx={{ position: "sticky", top: 0 + offset, zIndex: zIndex }}>{children}</Box>
    </Slide>
  )
}
